body {
  margin: 0;
  padding: 0;
  font-family: "Space Mono";
}

.justify-center {
  display: flex;
  justify-content: center;
}

.justify-start {
  display: flex;
  justify-content: flex-start;
}

.justify-end {
  justify-content: flex-end;
}

.align-center {
  display: flex;
  align-items: center;
}

.align-start {
  display: flex;
  align-items: flex-start;
}

.grow {
  flex-grow: 1;
}

.container {
  width: 100%;
  height: calc(100vh - 50px);
  padding-top: 50px;
  position: relative;
}

.item {
  color: white;
  margin: 10px;
  padding: 1rem;
  border-radius: 8px;
  text-align: center;
  font-weight: bold;
}

input[type=submit] {
  font-family: "Space Mono";
  color: white;
  background-color: red;
  margin: 10px;
  padding: 1rem 1.5rem;
  border-radius: 8px;
  text-align: center;
  font-weight: bold;
  border: 2px red solid;
}

input[type=text] {
  font-family: "Space Mono";
  flex-grow: 0.25;
  color: red;
  margin: 10px;
  padding: 1rem;
  border-radius: 8px;
  text-align: center;
  font-weight: bold;
  border: 2px red solid;
}

.container span {
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  text-align: center;
  padding-top: 20px;
  opacity: 0.5;
}